import {
  BillingClientId,
  ChangeReasonCode,
  ProjectReportId,
  ReportText,
  Title,
} from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectBudgetChange extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
  changeReasonCode: ChangeReasonCode;
  billingClientId: BillingClientId;
}
export interface IUpdateProjectBudgetChange extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
  changeReasonCode?: ChangeReasonCode;
  billingClientId?: BillingClientId;
}

export class ProjectBudgetChange extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;
  readonly changeReasonCode: ChangeReasonCode;
  readonly billingClientId: BillingClientId;

  constructor(args: IProjectBudgetChange) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.changeReasonCode = args.changeReasonCode;
    this.billingClientId = args.billingClientId;
  }

  update(args: IUpdateProjectBudgetChange): UpdateProjectBudgetChange {
    return new UpdateProjectBudgetChange({ id: this.id, ...args });
  }
}

export class UpdateProjectBudgetChange extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;
  readonly changeReasonCode?: ChangeReasonCode;
  readonly billingClientId?: BillingClientId;

  constructor(args: IUpdateProjectBudgetChange & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.changeReasonCode = args.changeReasonCode;
    this.billingClientId = args.billingClientId;
  }
}
